import { Component, Host, State, h } from '@stencil/core';

@Component({
  tag: 'home-page',
  styleUrl: 'home-page.css',
  shadow: true,
})
export class HomePage {

  userRef: any;
  @State() userName: any = "";

  setUserName() {
    this.userName =  this.userRef.value;
  }
  render() {
    return (
      <Host>
        <slot>
          {
            this.userName?.toString()?.length > 0 ? <call-user userNameProps={this.userName}/> : <div>
            <input ref={e => this.userRef = e} type='text' />
            <button onClick={() => this.setUserName()}>Set Username</button>
          </div>
          }
        </slot>
      </Host>
    );
  }

}
